:root {
  --background: #f1f1f1;
  --dark: black;
  --middle: #0f3d3e;
  --light: #e2dcc8;
}
html {
  background-color: var(--middle);
}
.headerALink {
  width: fit-content;
  padding-left: 5%;
  color: var(--light);
}

.link {
  color: var(--light);
  background-color: var(--middle);
  padding: 5px;
  font-size: 25px;
  margin: 10px;
  border: 2px solid var(--dark);
  border-radius: 5px;
  text-decoration: none;
}
.linkListDiv {
  padding: 15px;
}
.link:hover {
  color: var(--middle);
  background-color: var(--light);
}
.linkList {
  text-align: center;
}
p {
  color: var(--light);
  font-size: larger;
  padding-left: 5%;
}
.titles {
  color: var(--light);
  font-size: 45px;
  text-align: center;
  font-weight: 800;
  margin-bottom: -30px;
}
.App {
  background-color: var(--light);
  margin-top: 5%;
}
.linkListDiv:hover {
  transform: skewY(-3deg);
}

.ProjectList {
  padding-top: 5%;
  display: grid;
  grid-template-columns: 20% 20% 20% 20% 20%;
  grid-auto-flow: row;
  padding-bottom: 3%;
}

.Project {
  border: solid var(--dark) 2px;
  border-radius: 20px;
  margin: 5%;
  background-color: var(--background);
  position: relative;
  height: 100%;
  text-align: center;
}

.Text {
  color: var(--background);
  margin: 1%;
}

.Img {
  width: 80%;
  border: 4px solid var(--middle);
  border-radius: 10px;
}

.Img:hover {
  width: 95%;
  position: relative;
}

.Project:hover {
  background-color: var(--middle);
  color: var(--background);
}
